import article_list from '@/view/gateway/article_list'
import article_cate from '@/view/gateway/article_cate'
import recruit from '@/view/gateway/recruit'
import qa_list from '@/view/gateway/qa_list'
import tourism from '@/view/gateway/tourism'

export default [
    {//文章管理
        path: 'article_list',
        name: 'article_list',
        component: article_list
    },
    {//文章管理
        path: 'article_cate',
        name: 'article_cate',
        component: article_cate
    },
    {//招聘设置
        path: 'recruit',
        name: 'recruit',
        component: recruit
    },
    {//问答管理
        path: 'qa_list',
        name: 'qa_list',
        component: qa_list
    },
    {//旅游管理
        path: 'tourism',
        name: 'tourism',
        component: tourism
    },
]



