import wcs_list from '@/view/active/wcs_list'
import wcs_cate from '@/view/active/wcs_cate'
import wcs_log from '@/view/active/wcs_log'
import vote_list from '@/view/active/vote_list'
import dzp_list from '@/view/active/dzp_list'

export default [
    {//微测试
        path: 'wcs_list',
        name: 'wcs_list',
        component: wcs_list
    },
    {//微测试
        path: 'wcs_cate',
        name: 'wcs_cate',
        component: wcs_cate
    },
    {//微测试
        path: 'wcs_log',
        name: 'wcs_log',
        component: wcs_log
    },
    {//微投票
        path: 'vote_list',
        name: 'vote_list',
        component: vote_list
    },
    {//大转盘
        path: 'dzp_list',
        name: 'dzp_list',
        component: dzp_list
    }
]



