<template>
    <div class="c_right_box">
        <div class="c_right_top">
            <a href="javascript:;" class="on">评论列表</a>
            <a href="javascript:;" @click="handleDelete()">删除广告</a>
        </div>
        <el-table
            ref="multipleTable"
            :data="list"
            tooltip-effect="dark"
            style="width: 100%"
            @selection-change="handleSelectionChange"
        >
            <el-table-column type="selection" ></el-table-column>
            <el-table-column label="排序" width="80">
                <template slot-scope="scope">
                    <el-input v-model="scope.row.rank" @blur="handleRank(scope.row.id,scope.row.rank)"></el-input>
                </template>
            </el-table-column>
            <el-table-column label="商品名称" prop="goods_name" width="200"></el-table-column>
            <el-table-column label="用户名" prop="name" width="200"></el-table-column>
            <el-table-column label="头像">
                <template slot-scope="scope">
                    <img :src="scope.row.headimg" alt="" height="80">
                </template>
            </el-table-column>
            <el-table-column label="评论内容" prop="content" width="200"></el-table-column>
            <el-table-column label="点赞数" prop="dz" width="200"></el-table-column>
            <el-table-column label="创建时间" prop="ctime" width="150"></el-table-column>
            <el-table-column label="操作" width="200">
                 <template slot-scope="scope">
                    <el-button
                    size="mini"
                    type="danger"
                    @click="handleDelete(scope.row.id)">删除</el-button>
                </template>
            </el-table-column>
        </el-table>
        <el-pagination
        @current-change="handleCurrentChange"
        :current-page="page"
        :page-size="10"
        layout="total,  prev, pager, next, jumper"
        :total="total">
        </el-pagination>
    </div>
</template>

<script type="text/javascript">
import {ajax,delAjax,rankAjax} from 'js/common'
export default {
    name: 'ad_list',
    data () {
        return {
            imageUrl: '',
            page:1,
            total:1,
            list:[],
            selectArr:[],
        }
	},
    created () {
        this.appData();
	},
    methods : {
        // 加载数据
		appData () {
            ajax('goods/goods_comment',{page:this.page} , rs => {
                this.list = rs.data.list;
                this.total = Number(rs.data.nums);
            });
        },
        // 翻页
        handleCurrentChange(val) {
            this.page = val;
            this.appData();
        },
        // 单条/多条删除
        handleDelete (id = 0) {
            delAjax(this , 'goods_comment' , id);
        },
        // 获取选中的id
        handleSelectionChange(val) {
            this.selectArr = val;
        },
        // 更改排序
        handleRank (id,rank) {
            rankAjax('goods_comment' , id , rank)
        }
    }
}
</script>    



