import goods_list from '@/view/goods/goods_list'
import goods_cate from '@/view/goods/goods_cate'
import goods_comment from '@/view/goods/goods_comment'
import order_list from '@/view/goods/order_list'
import ad_list from '@/view/goods/ad_list'

export default [
    {//商品管理
        path: 'goods_list',
        name: 'goods_list',
        component: goods_list
    },
    {//分类管理
        path: 'goods_cate',
        name: 'goods_cate',
        component: goods_cate
    },
    {//评论管理
        path: 'goods_comment',
        name: 'goods_comment',
        component: goods_comment
    },
    {//订单管理
        path: 'order_list',
        name: 'order_list',
        component: order_list
    },
    {//广告管理
        path: 'ad_list',
        name: 'ad_list',
        component: ad_list
    },
]



