<template>
    <div class="c_right_box">
        <div class="c_right_top">
            <router-link to="dzp_list" class="on">投票列表</router-link>
            <a href="javascript:;" @click="handleEdit()">添加投票</a>
            <a href="javascript:;" @click="handleDelete()">删除投票</a>
        </div>
        <el-table
            ref="multipleTable"
            :data="list"
            tooltip-effect="dark"
            style="width: 100%"
            @selection-change="handleSelectionChange"
        >
            <el-table-column type="selection" ></el-table-column>
            <el-table-column label="标题" prop="title" width="200"></el-table-column>
            <el-table-column label="封面">
                <template slot-scope="scope">
                    <img :src="scope.row.cover" alt="" height="80">
                </template>
            </el-table-column>
            <el-table-column label="开始时间" prop="start_time" width="150"></el-table-column>
            <el-table-column label="结束时间" prop="end_time" width="150"></el-table-column>
            <el-table-column label="状态" width="80">
                <template slot-scope="scope">
                    <el-switch
                    v-model="scope.row.status"
                    active-value=1
                    inactive-value=2
                    active-color="#13ce66"
                    inactive-color="#999999"
                    @change="handleSwitch(scope.row.id,scope.row.status,'status')"
                    >
                    </el-switch>
                </template>
            </el-table-column>
            <el-table-column label="创建时间" prop="ctime" width="150"></el-table-column>
            <el-table-column label="操作" width="200">
                 <template slot-scope="scope">
                    <el-button
                    size="mini"
                    @click="handleEdit(scope.row)">编辑</el-button>
                    <el-button
                    size="mini"
                    type="danger"
                    @click="handleDelete(scope.row.id)">删除</el-button>
                </template>
            </el-table-column>
        </el-table>
        <el-pagination
        @current-change="handleCurrentChange"
        :current-page="page"
        :page-size="10"
        layout="total,  prev, pager, next, jumper"
        :total="total">
        </el-pagination>

        <el-dialog :title="formMsg" :visible.sync="dialogFormVisible">
            <el-form :model="form">
                <el-form-item label="标题" :label-width="labelWidth">
                    <el-input v-model="form.title" style="width:200px;"></el-input>
                </el-form-item>
                <el-form-item label="封面" :label-width="labelWidth">
                    <upload ref="cover" :image="cover"></upload>
                </el-form-item>
                <el-form-item label="活动时间" :label-width="labelWidth">
                    <el-date-picker
                    v-model="times"
                    value-format="yyyy-MM-dd HH:mm:ss"
                    type="datetimerange"
                    align="right"
                    start-placeholder="开始日期"
                    end-placeholder="结束日期"
                    :default-time="['12:00:00', '08:00:00']">
                    </el-date-picker>
                </el-form-item>
                <el-form-item label="限制天数" :label-width="labelWidth">
                    <el-input v-model="form.limit_type" style="width:200px;"></el-input>
                </el-form-item>
                <el-form-item label="可玩次数" :label-width="labelWidth">
                    <el-input v-model="form.limit_num" style="width:200px;"></el-input>
                </el-form-item>
                <el-form-item label="简要" :label-width="labelWidth">
                    <el-input v-model="form.content" type="textarea" style="max-width:400px;"></el-input>
                </el-form-item>
                
                <el-input v-model="form.id" type="hidden"></el-input>
            </el-form>
            <div slot="footer" class="dialog-footer">
                <el-button @click="dialogFormVisible = false">取 消</el-button>
                <el-button type="primary" @click="submit">确 定</el-button>
            </div>
        </el-dialog>
    </div>
</template>

<script type="text/javascript">
import {ajax,delAjax,switchAjax} from 'js/common'
export default {
    name: 'vote_list',
    data () {
        return {
            page:1,
            total:1,
            list:[],
            selectArr:[],
            dialogFormVisible:false,
            form:{},
            formMsg:'',
            labelWidth:'80px',
            cover:'',
            times: '',
        }
	},
    created () {
        this.appData();
	},
    methods : {
        // 加载数据
		appData () {
            ajax('active/vote_list',{page:this.page} , rs => {
                this.list = rs.data.list;
                this.total = Number(rs.data.nums);
            });
        },
        // 翻页
        handleCurrentChange(val) {
            this.page = val;
            this.appData();
        },
        // 单条/多条删除
        handleDelete (id = 0) {
            delAjax(this , 'vote' , id);
        },
        // 获取选中的id
        handleSelectionChange(val) {
            this.selectArr = val;
        },
        // 状态开关
        handleSwitch (id , val , field) {
            switchAjax('vote' , id , val , field)
        },
        // 添加&编辑
        handleEdit (row = ''){
            this.dialogFormVisible = true;
            this.form = row ? row : {};
            this.formMsg = (row ? '修改' : '添加')+'投票';
            this.cover = row ? row.cover : '';
            if(row){
                this.times = [];
                for(var i = 0 ; i < 2 ; i++){
                    this.times.push( i == 0 ? row.start_time : row.end_time);
                }
            }else{
                this.times = '';
            }
        },
        // 提交
        submit () {
            this.form.start_time = this.times[0];
            this.form.end_time = this.times[1];
            this.form.cover = this.$refs.cover.imgSrc;
            ajax('active/voteSubmit', this.form , rs => {
                if(rs.ret == 1){
                    var message = (this.form.id ? '修改' : '添加')+'成功!';
                    this.$message({
                        showClose: true,
                        type: 'success',
                        message: message
                    });
                    this.dialogFormVisible = false;
                    this.appData();
                }else{
                    this.$message({
                        showClose: true,
                        type: 'error',
                        message: rs.msg
                    });
                }
            });
        }
    }
}
</script>    



