<template>
    <div class="c_right_box">
        <div class="c_right_top">
            <a href="javascript:;" class="on">全部订单</a>
            <a href="javascript:;" @click="handleDelete()">删除订单</a>
        </div>
        <el-table
            ref="multipleTable"
            :data="list"
            tooltip-effect="dark"
            style="width: 100%"
            @selection-change="handleSelectionChange"
        >
            <el-table-column type="selection" ></el-table-column>
            <el-table-column label="订单号" prop="order_nums" width="200"></el-table-column>
            <el-table-column label="买家昵称" prop="name" width="200"></el-table-column>
            <el-table-column label="积分" prop="amount" width="200"></el-table-column>
            <el-table-column label="状态" prop="goods_status" width="200"></el-table-column>
            <el-table-column label="下单时间" prop="ctime" width="150"></el-table-column>
        </el-table>
        <el-pagination
        @current-change="handleCurrentChange"
        :current-page="page"
        :page-size="10"
        layout="total,  prev, pager, next, jumper"
        :total="total">
        </el-pagination>
    </div>
</template>

<script type="text/javascript">
import {ajax} from 'js/common'
export default {
    name: 'order_list',
    data () {
        return {
            imageUrl: '',
            page:1,
            total:1,
            list:[],
            selectArr:[],
        }
	},
    created () {
        this.appData();
	},
    methods : {
        // 加载数据
		appData () {
            ajax('goods/order_list',{page:this.page} , rs => {
                this.list = rs.data.list;
                this.total = Number(rs.data.nums);
            });
        },
        // 翻页
        handleCurrentChange(val) {
            this.page = val;
            this.appData();
        },
        // 获取选中的id
        handleSelectionChange(val) {
            this.selectArr = val;
        }
    }
}
</script>    



